import React, {useEffect, useState} from "react";
import CodeList from "../components/CodeList";

import {useParams} from "react-router-dom";
import {useHttpClient} from "../../shared/hooks/http-hook";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";

const UserCodes = () => {
    const userId = useParams().userId;

    const [loadedCodes, setLoadedCodes] = useState();
    const {isLoading, error, sendRequest, clearError} = useHttpClient();

    useEffect(() => {
        const fetchCodes =  async () => {
            try {
                const responseData = await sendRequest( process.env.REACT_APP_BACKEND_URL + `/codes/user/${userId}`);
                setLoadedCodes(responseData.codes);
            }catch (e) {}
        }
        fetchCodes();
    }, [sendRequest, userId])

    const codeDeletedHandler = (deletedCodeId) => {
        setLoadedCodes(prevState => prevState.filter(code => code.id !== deletedCodeId))
    }

    return (
        <>
            <ErrorModal error={error} onClear={clearError} />
            {isLoading && <div className="center">
                <LoadingSpinner asOverlay />
            </div>}
            {!isLoading && loadedCodes && <CodeList items={loadedCodes} onDeleteCode={codeDeletedHandler}/>}
        </>
    )
}

export default UserCodes;
