import React, {useContext, useEffect, useState} from "react";
import Prism from "prismjs";
import './CodeItem.css';
import Card from "../../shared/components/UIElements/Card";
import Code from "./Code";
import Button from "../../shared/components/FormElements/Button";
import Modal from "../../shared/components/UIElements/Modal";
import {AuthContext} from "../../shared/context/auth-context";
import {useHttpClient} from "../../shared/hooks/http-hook";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";

const CodeItem = (props) => {
    const {isLoading, error, sendRequest, clearError} = useHttpClient();
    const auth = useContext(AuthContext);
    const [showConfirmModal, setShowConfirmModal] = useState(false);

    const showDeleteHandler = () => {
      setShowConfirmModal(true)
    }
    const canceledDeleteHandler = () => {
        setShowConfirmModal(false)
    }

    const confirmDeleteHandler =  async () => {
        setShowConfirmModal(false)
        try {
            await sendRequest(process.env.REACT_APP_BACKEND_URL + `/codes/${props.id}`, 'DELETE');
            props.onDelete(props.id);
        }catch (e) {

        }
    }

    useEffect(() => {
        Prism.highlightAll();
    }, []);
    return <>
        <ErrorModal error={error} onClear={clearError}/>
        <Modal show={showConfirmModal}
               onCancel={canceledDeleteHandler}
            header=" ?"
               footerClass="code-item__modal-actions"
               footer={
                   <>
                       <Button inverse onClick={canceledDeleteHandler}>Cancel</Button>
                       <Button danger onClick={confirmDeleteHandler}>Delete</Button>
                   </>
               }>
            <p>Are you sure you want to delete the code?</p>
        </Modal>
        <li className="code-item">
            <Card className="code-item__content">
                {isLoading && <LoadingSpinner asOverlay />}
                <div>
                    <h2>{props.title}</h2>
                    <Code code={props.cod} language="javascript" />
                </div>
                <div className="code-item__actions">
                    {auth.userId === props.creator &&  (<Button onClick={showDeleteHandler} danger>
                    Delete
                    </Button>)}
                    {auth.userId === props.creator && (<Button to={`/codes/${props.id}`}>
                    Update
                    </Button>) }
                </div>
            </Card>
        </li>
    </>
}

export default CodeItem;
