import React, {useCallback, useState} from 'react';

import { BrowserRouter as Router, Route, Redirect, Switch} from "react-router-dom";
import {Users} from "./users/pages/Users";
import NewCode from "./codes/pages/NewCode";
import MainNavigation from "./shared/components/Navigation/MainNavigation";
import UserCodes from "./codes/pages/UserCodes";
import UpdateCode from "./codes/pages/UpdateCode";
import Auth from "./users/pages/Auth";
import {AuthContext} from "./shared/context/auth-context";


const App = () => {

    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [userId, setUserId] = useState(false);

    const login = useCallback((uid) => {
            setIsLoggedIn(true)
        setUserId(uid)

    }, [])

    const logout = useCallback(() => {
        setIsLoggedIn(false)
        setUserId(null)

    }, [])

    let routes;

    if (isLoggedIn) {
        routes = (
            <Switch>
                <Route path="/" exact>
                    <Users />
                </Route>
                <Route path="/:userId/codes" exact>
                    <UserCodes />
                </Route>
                <Route path="/codes/new" exact>
                    <NewCode />
                </Route>
                <Route path="/codes/:codeId">
                    <UpdateCode />
                </Route>
                <Redirect to="/" />
            </Switch>
        )
    } else {
        routes = (
            <Switch>
            <Route path="/" exact>
                <Users />
            </Route>
        <Route path="/:userId/codes" exact>
            <UserCodes />
        </Route>
                <Route path="/auth">
                    <Auth />
                </Route>
                <Redirect to="/auth" />
            </Switch>
        )
    }

    return (
        <AuthContext.Provider value={{
            isLoggedIn: isLoggedIn,
            userId: userId,
            login: login,
            logout: logout
        }}>
        <Router>
        <MainNavigation />
        <main>
            {routes}
        </main>
    </Router>
        </AuthContext.Provider>)
};

export default App;
