import React, {useContext, useEffect, useState} from 'react';

import {useHistory, useParams} from "react-router-dom";
import Input from "../../shared/components/FormElements/Input";
import Button from "../../shared/components/FormElements/Button";
import {VALIDATOR_MINLENGTH, VALIDATOR_REQUIRE} from "../../shared/util/validators";

import './NewCode.css';
import {useFrom} from "../../shared/hooks/form-hook";
import Card from "../../shared/components/UIElements/Card";
import {useHttpClient} from "../../shared/hooks/http-hook";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import {AuthContext} from "../../shared/context/auth-context";


const UpdateCode = (props) => {
    const {isLoading, error, sendRequest, clearError} = useHttpClient();
    const [loadedCode, setLoadedCode] = useState();
    const history = useHistory();
    const auth = useContext(AuthContext)

    const codeId = useParams().codeId;

    const [formState, inputHandler, setFormData] = useFrom({
        title: {
            value: '',
            isValid: false
        },
        code: {
            value: '',
            isValid: false
        },
    }, false)

    useEffect(() => {
        const fetchCode = async () => {
            try {
            const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL + `/codes/${codeId}`);
            setLoadedCode(responseData.code);
            setFormData({title: {
                        value: responseData.code.title,
                        isValid: true
                    },
                    code: {
                        value: responseData.code.code,
                        isValid: true
                    },}, true)
            }catch (err){}
        }
        fetchCode();
    }, [sendRequest, codeId, setFormData])

    const codeSubmitHandler = async (e) => {
        e.preventDefault();
        try {
            await sendRequest(process.env.REACT_APP_BACKEND_URL +  `/codes/${codeId}`, 'PATCH', JSON.stringify({
                title: formState.inputs.title.value,
                code: formState.inputs.code.value,
            }), {
                'Content-Type': 'application/json'
            })
            history.push('/' + auth.userId + '/codes')
        }catch (err){}
    }

    if (isLoading) {
        return <div className="center">
            <LoadingSpinner />
        </div>
    }

    if (!loadedCode && !error) {
        return <div className="center">
            <Card>
            <h2>Can't find code(</h2>
            </Card>
        </div>
    }


    return (
        <>
            <ErrorModal  error={error} onClear={clearError}/>
            {!isLoading && loadedCode && (
                <form className="code-form" onSubmit={codeSubmitHandler}>
                    <Input id="title"
                           element="input"
                           type="text"
                           label=" Morning Task"
                           validators={[VALIDATOR_REQUIRE()]}
                           errorText="Please provide name"
                           onInput={inputHandler}
                           initialValue={loadedCode.title}
                           initialValid={true}
                    />
                    <Input element="textarea"
                           id="code"
                           label=""
                           rows="28"
                           validators={[VALIDATOR_REQUIRE(), VALIDATOR_MINLENGTH(15)]}
                           errorText="Please write the code (minimum 15 characters)"
                           initialValue={loadedCode.code}
                           initialValid={true}
                           onInput={inputHandler}
                    />
                    <Button type="submit" disabled={!formState.isValid}>Update Code</Button>
                </form>
            )}
        </>
    )
};

export default UpdateCode;
