import React, {useContext, useState} from 'react';
import Card from "../../shared/components/UIElements/Card";
import Input from "../../shared/components/FormElements/Input";
import Button from "../../shared/components/FormElements/Button";
import {VALIDATOR_EMAIL, VALIDATOR_REQUIRE} from "../../shared/util/validators";
import {useFrom} from "../../shared/hooks/form-hook";

import './Auth.css';
import {AuthContext} from "../../shared/context/auth-context";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import {useHttpClient} from "../../shared/hooks/http-hook";

const Auth = () => {
    const [isLogin, setIsLoginMode] = useState(true);
    const {isLoading, error, sendRequest, clearError} = useHttpClient();

    const auth = useContext(AuthContext)

    const [formState, inputHandler, setFormData] = useFrom({
        email: {
            value: '',
            isValid: false
        },
        password: {
            value: '',
            isValid: false
        }
    }, false)

    const modeHandler = () => {
        if (!isLogin) {
            setFormData(
                {
                ...formState.inputs,
                name: undefined,
                },
                formState.inputs.email.isValid && formState.inputs.password.isValid )
        } else {
            setFormData({
                ...formState.inputs,
                name: {
                    value: '',
                    isValid: false,
                }
            } , false)
        }
        setIsLoginMode(prevMode => !prevMode)
    }

    const authSubmitHandler = async (e) => {
      e.preventDefault();
        if(isLogin) {
            try{
                const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL + '/users/login', "POST",
                    JSON.stringify({
                        email: formState.inputs.email.value,
                        password: formState.inputs.password.value,
                    }),
                    {
                        'Content-Type': 'application/json'
                    },
                );
                auth.login(responseData.user.id)
            }catch (err){
            }
        } else{
            try{
                const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL + '/users/signup', "POST",
                   JSON.stringify({
                       name: formState.inputs.name.value,
                       email: formState.inputs.email.value,
                       password: formState.inputs.password.value,
                   }),
                 {
                     'Content-Type': 'application/json'
                 }
             );
                auth.login(responseData.user.id)
            }catch (err){
            }
        }
    }

    return <>
        <ErrorModal  error={error} onClear={clearError}/>
        <Card className="authentication">
            {isLoading && <LoadingSpinner asOverlay />}
            <h2>Login please!</h2>
            <hr/>
            <form onSubmit={authSubmitHandler}>
                {!isLogin && <Input  element="input"
                                     id="name"
                                     type="text"
                                     label="Name"
                                     validators={[VALIDATOR_REQUIRE()]}
                                     errorText="Input name"
                                     onInput={inputHandler}
                />}
                <Input element="input"
                       id="email"
                       type="email"
                       label="E-mail"
                       validators={[VALIDATOR_EMAIL()]}
                       errorText=" email"
                       onInput={inputHandler}
                />
                <Input element="input"
                       id="password"
                       type="password"
                       label="Password"
                       validators={[VALIDATOR_REQUIRE(7)]}
                       errorText="Input password(min 7 symbols)"
                       onInput={inputHandler}
                />
                <Button type="submit" disabled={!formState.isValid}>
                    {isLogin ?  'Login' : 'SignUp'}
                </Button>
            </form>
            <Button inverse onClick={modeHandler}>
                Go to {isLogin ? 'SignUp' : 'Login'}
            </Button>
        </Card>
    </>
};

export default Auth;
