import React from "react";

import './CodeList.css';
import Card from "../../shared/components/UIElements/Card";
import CodeItem from "./CodeItem";
import Button from "../../shared/components/FormElements/Button";

const CodeList = (props) => {
    if (props.items.length === 0) {
        return (
            <div>
                <Card className="code-list center">
                    <h2>You haven't shared your decision with anyone yet! Can you add?</h2>
                    <Button to="/codes/new">Share Solution</Button>
                </Card>
            </div>
        )
    }

    return (
        <ul className="code-list">
            {props.items.map(code => <CodeItem key={code.id}
                                               id={code.id}
                                               title={code.title}
                                               cod={code.code}
                                               creator={code.creator}
                                               onDelete={props.onDeleteCode}
            />)}
        </ul>
    )
}

export default CodeList;
